<template>
  <div class="bg-tkp h-100">
    <student-mark-collection
      v-if="TranscriptState.student && !data.loading"
      :Transcript="TranscriptState"
    />
    <circulate-load v-else-if="data.loading" />
    <can-not-find v-else :what="`This transcript`" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { defaultData } from "@/utils/data.js";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import StudentMarkCollection from "../../components/universal/StudentMarkCollection.vue";
export default {
  components: { StudentMarkCollection, CirculateLoad, CanNotFind },
  computed: {
    ...mapGetters(["ErrorBag", "TranscriptState"]),
  },
  methods: {
    ...mapActions(["studentCollectNotes"]),
    async Initialize() {
      this.data.loading = true;
      try {
        const schema = {
          id: this.$route.params.id,
        };
        await this.studentCollectNotes(schema);
      } catch (error) {
        console.log(error);
      }
      this.data.loading = false;
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      data: defaultData,
    };
  },
};
</script>

<style></style>
